export default function Icon({ w, h, color, rotate, loc }) {
  const style = () => {
    if (loc === "leftSmall") {
      return {
        position: "absolute",
        top: "0.05rem",
        left: "0rem"
      };
    }
    if (loc === "rightSmall") {
      return {
        position: "absolute",
        bottom: "0.475rem",
        right: "0rem"
      };
    }
    if (loc === "left") {
      return {
        position: "absolute",
        top: "-.3rem",
        left: "-.6rem"
      };
    }
    if (loc === "right") {
      return {
        position: "absolute",
        bottom: "-.3rem",
        right: "-.6rem"
      };
    }
  };

  return (
    <svg
      aria-hidden="true"
      width={w}
      height={h}
      viewBox="0 0 100 100"
      style={style()}
    >
      <path
        fill="none"
        transform={rotate}
        style={{
          WebkitTransformOrigin: "50%50%",
          MsTransformOrigin: "50%50%",
          transformOrigin: "50%50%"
        }}
        stroke={color}
        strokeWidth="8"
        d="M10 10L90 10 90 90"
      ></path>
    </svg>
  );
}
